.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


p{
  font-size:12px;
}
h1{
  font-size: 30px;
}
.first_css_in_React {
  font-size: 15px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  margin-top: -15px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.cover{
  display: flex;
  justify-content: center;
  align-items: center;
}

table {
  margin-left:auto; 
  margin-right:auto;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
  
}

.game {
  display: flex;
  flex-direction: row;
}